'use client'

import { useState } from 'react'
import { Mail, Phone, MapPin, Edit2, Save, X, Camera, LogOut } from 'lucide-react'
import { PageLayout } from '@/components/page-layout'
import { AnimatedButton } from '@/components/animated-button'

interface UserProfile {
  name: string
  email: string
  phone: string
  location: string
  bio: string
  joinDate: string
  coursesEnrolled: number
  certificatesEarned: number
  totalHours: number
}

export default function ProfilePage() {
  const [isEditing, setIsEditing] = useState(false)
  const [profile, setProfile] = useState<UserProfile>({
    name: 'Alex Johnson',
    email: 'alex.johnson@example.com',
    phone: '+1 (555) 123-4567',
    location: 'San Francisco, CA',
    bio: 'Passionate learner and full-stack developer. Always exploring new technologies and sharing knowledge with the community.',
    joinDate: 'January 15, 2024',
    coursesEnrolled: 12,
    certificatesEarned: 3,
    totalHours: 145,
  })

  const [formData, setFormData] = useState(profile)

  const handleInputChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target
    setFormData((prev) => ({ ...prev, [name]: value }))
  }

  const handleSave = () => {
    setProfile(formData)
    setIsEditing(false)
  }

  const handleCancel = () => {
    setFormData(profile)
    setIsEditing(false)
  }

  return (
    <PageLayout>
      <div className="p-4 md:p-8 max-w-4xl">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-4xl md:text-5xl font-bold mb-2">My Profile</h1>
          <p className="text-muted-foreground">Manage your account and learning preferences</p>
        </div>

        {/* Profile Card */}
        <div className="mb-8 p-8 rounded-lg border border-border bg-card">
          {/* Avatar Section */}
          <div className="flex flex-col md:flex-row gap-8 items-start md:items-center pb-8 border-b border-border mb-8">
            <div className="relative">
              <div className="w-32 h-32 rounded-full bg-gradient-to-br from-primary to-accent glow-blue flex items-center justify-center text-4xl font-bold">
                {profile.name.charAt(0)}
              </div>
              {isEditing && (
                <button className="absolute bottom-2 right-2 p-3 rounded-full bg-primary text-primary-foreground hover:bg-primary/90 transition-all">
                  <Camera size={18} />
                </button>
              )}
            </div>

            <div className="flex-1">
              {isEditing ? (
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleInputChange}
                  className="text-3xl font-bold mb-2 w-full px-3 py-2 rounded-lg border border-border bg-input focus:outline-none focus:ring-2 focus:ring-primary"
                />
              ) : (
                <h2 className="text-3xl font-bold mb-2">{profile.name}</h2>
              )}
              <p className="text-muted-foreground mb-4">Member since {profile.joinDate}</p>
              {!isEditing && (
                <button
                  onClick={() => setIsEditing(true)}
                  className="flex items-center gap-2 px-4 py-2 rounded-lg border border-border hover:bg-card transition-all font-medium text-sm"
                >
                  <Edit2 size={16} />
                  Edit Profile
                </button>
              )}
            </div>
          </div>

          {/* Stats Section */}
          <div className="grid grid-cols-3 gap-4 mb-8">
            <div className="text-center p-4 rounded-lg bg-background/50">
              <p className="text-2xl font-bold text-primary mb-1">{profile.coursesEnrolled}</p>
              <p className="text-xs text-muted-foreground">Courses Enrolled</p>
            </div>
            <div className="text-center p-4 rounded-lg bg-background/50">
              <p className="text-2xl font-bold text-accent mb-1">{profile.certificatesEarned}</p>
              <p className="text-xs text-muted-foreground">Certificates</p>
            </div>
            <div className="text-center p-4 rounded-lg bg-background/50">
              <p className="text-2xl font-bold text-secondary mb-1">{profile.totalHours}h</p>
              <p className="text-xs text-muted-foreground">Total Hours</p>
            </div>
          </div>

          {/* Contact Information */}
          <div className="space-y-4 mb-8">
            <h3 className="text-lg font-bold">Contact Information</h3>

            <div>
              <label className="text-sm font-semibold text-muted-foreground mb-2 block">Email</label>
              <div className="flex items-center gap-2">
                <Mail size={18} className="text-muted-foreground" />
                {isEditing ? (
                  <input
                    type="email"
                    name="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    className="flex-1 px-3 py-2 rounded-lg border border-border bg-input focus:outline-none focus:ring-2 focus:ring-primary text-sm"
                  />
                ) : (
                  <p>{profile.email}</p>
                )}
              </div>
            </div>

            <div>
              <label className="text-sm font-semibold text-muted-foreground mb-2 block">Phone</label>
              <div className="flex items-center gap-2">
                <Phone size={18} className="text-muted-foreground" />
                {isEditing ? (
                  <input
                    type="tel"
                    name="phone"
                    value={formData.phone}
                    onChange={handleInputChange}
                    className="flex-1 px-3 py-2 rounded-lg border border-border bg-input focus:outline-none focus:ring-2 focus:ring-primary text-sm"
                  />
                ) : (
                  <p>{profile.phone}</p>
                )}
              </div>
            </div>

            <div>
              <label className="text-sm font-semibold text-muted-foreground mb-2 block">Location</label>
              <div className="flex items-center gap-2">
                <MapPin size={18} className="text-muted-foreground" />
                {isEditing ? (
                  <input
                    type="text"
                    name="location"
                    value={formData.location}
                    onChange={handleInputChange}
                    className="flex-1 px-3 py-2 rounded-lg border border-border bg-input focus:outline-none focus:ring-2 focus:ring-primary text-sm"
                  />
                ) : (
                  <p>{profile.location}</p>
                )}
              </div>
            </div>
          </div>

          {/* Bio Section */}
          <div className="mb-8">
            <label className="text-sm font-semibold text-muted-foreground mb-2 block">Bio</label>
            {isEditing ? (
              <textarea
                name="bio"
                value={formData.bio}
                onChange={handleInputChange}
                rows={4}
                className="w-full px-3 py-2 rounded-lg border border-border bg-input focus:outline-none focus:ring-2 focus:ring-primary text-sm resize-none"
              />
            ) : (
              <p className="text-foreground/80 leading-relaxed">{profile.bio}</p>
            )}
          </div>

          {/* Action Buttons */}
          <div className="flex flex-col sm:flex-row gap-4 pt-8 border-t border-border">
            {isEditing ? (
              <>
                <AnimatedButton onClick={handleSave} size="lg" className="flex-1 sm:flex-none">
                  <Save size={18} className="mr-2 inline-block" />
                  Save Changes
                </AnimatedButton>
                <button
                  onClick={handleCancel}
                  className="flex-1 sm:flex-none px-6 py-3 rounded-lg border border-border hover:bg-card transition-all font-semibold flex items-center justify-center gap-2"
                >
                  <X size={18} />
                  Cancel
                </button>
              </>
            ) : (
              <button className="flex items-center justify-center gap-2 px-6 py-3 rounded-lg border border-destructive text-destructive hover:bg-destructive/10 transition-all font-semibold ml-auto">
                <LogOut size={18} />
                Logout
              </button>
            )}
          </div>
        </div>

        {/* Preferences Section */}
        <div className="p-8 rounded-lg border border-border bg-card">
          <h3 className="text-xl font-bold mb-6">Preferences</h3>

          <div className="space-y-4">
            <div className="flex items-center justify-between p-4 rounded-lg bg-background/50">
              <div>
                <p className="font-medium">Email Notifications</p>
                <p className="text-sm text-muted-foreground">Get updates about new courses and progress</p>
              </div>
              <input type="checkbox" defaultChecked className="w-5 h-5 rounded cursor-pointer" />
            </div>

            <div className="flex items-center justify-between p-4 rounded-lg bg-background/50">
              <div>
                <p className="font-medium">Marketing Emails</p>
                <p className="text-sm text-muted-foreground">Receive exclusive offers and promotions</p>
              </div>
              <input type="checkbox" className="w-5 h-5 rounded cursor-pointer" />
            </div>

            <div className="flex items-center justify-between p-4 rounded-lg bg-background/50">
              <div>
                <p className="font-medium">Course Recommendations</p>
                <p className="text-sm text-muted-foreground">Personalized course suggestions</p>
              </div>
              <input type="checkbox" defaultChecked className="w-5 h-5 rounded cursor-pointer" />
            </div>
          </div>
        </div>
      </div>
    </PageLayout>
  )
}
